export default function addReferences(node, childrenKey) {
  // add firstChild reference.
  if (node[childrenKey]) {
    node.firstChild = node[childrenKey][0];
  } // add parent reference.
  // add nextSibling reference.


  if (node[childrenKey]) {
    for (var i = 0; i < node[childrenKey].length; i++) {
      node[childrenKey][i].parentNode = node;

      if (node[childrenKey][i + 1]) {
        node[childrenKey][i].nextSibling = node[childrenKey][i + 1];
      }

      addReferences(node[childrenKey][i], childrenKey);
    }
  }
}
