import PRE from "./_log-prefix";
var DEFAULT_TYPE = "application/json";

function transform(response, type) {
  console.log("".concat(PRE, " Response:"), response);

  if (type !== "application/json") {
    type = type.replace(/[\w\d-.]+$/, "");
  }

  if (response.ok) {
    switch (type) {
      case "application/json":
        response = response.json();
        break;

      case "image/":
        response = response.blob();
        break;

      case "text/":
        response = response.text();
        break;

      case "audio/":
      case "video/":
        response = response.arrayBuffer();
        break;
    }

    return response;
  } else {
    throw new Error("".concat(PRE, " Network response was not ok. Response status: ").concat(response.status, ": ").concat(response.statusText, "."));
  }
}

function checkRequiredParameters(config) {
  // required parameters.
  var url = config.url,
      token = config.token;
  if (!url) throw new Error("".concat(PRE, " Empty parameter 'url'."));
  if (!token) throw new Error("".concat(PRE, " Empty parameter 'token'."));
}

function getMimeType(config) {
  // type handling.
  var url = config.url;
  var type = config.type;
  type = type ? type : DEFAULT_TYPE;

  if (type === "image") {
    // a general approach to support image mime types, does not support all of them.
    // the regex would need to get extended plus special case treatments.
    // you could also create a static table [extensions][mime-string] for a general solution.
    // mime reference: https://www.iana.org/assignments/media-types/media-types.xhtml#image
    var fileExt = url.match(/\.([\w]+)$/);

    if (fileExt) {
      fileExt = fileExt[1];

      if (fileExt === "svg") {
        type = "image/svg+xml";
      } else if (fileExt === "jpg") {
        type = "image/jpeg";
      } else {
        type = "image/".concat(fileExt);
      }
    } else {
      throw new Error("".concat(PRE, " error when detecting image mime type."));
    }
  }

  return type;
}

export function get(config) {
  checkRequiredParameters(config);
  var type = getMimeType(config);
  var url = config.url,
      token = config.token;
  var request = new Request(url, {
    method: "GET",
    // credentials: "include",
    headers: new Headers({
      "Content-Type": type,
      "Authorization": "Bearer ".concat(token)
    })
  });
  console.log("".concat(PRE, " GET: ").concat(url));
  return fetch(request).then(function (response) {
    return transform(response, type);
  });
}
export function post(config) {
  checkRequiredParameters(config);
  var type = getMimeType(config);
  var url = config.url,
      token = config.token; // handle data payload.

  var data = config.data;
  data = JSON.stringify(data ? data : {});
  var request = new Request(url, {
    method: "POST",
    // credentials: "include",
    headers: new Headers({
      "Content-Type": type,
      "Authorization": "Bearer ".concat(token)
    }),
    body: data
  });
  console.log("".concat(PRE, " POST: ").concat(url));
  return fetch(request).then(function (response) {
    return transform(response, type);
  });
}
