export function setTranslation(node, coordinate) {
  var x = coordinate.x,
      y = coordinate.y;
  x = x !== undefined ? x : getTranslationX(node);
  y = y !== undefined ? y : getTranslationY(node);
  node.setAttribute("transform", "translate(".concat(x, ",").concat(y, ")"));
} // export function setViewBox(node, attribute: String | any, value: Number){

export function setViewBox(node, attribute, value) {
  var vbString = node.getAttribute("viewBox");

  if (vbString) {
    if (typeof attribute === "string") {
      var vbArray = vbString.split(" ");

      switch (attribute.toLocaleLowerCase()) {
        case "x":
          vbArray[0] = value;
          break;

        case "y":
          vbArray[1] = value;
          break;

        case "w":
        case "width":
          vbArray[2] = value;
          break;

        case "h":
        case "height":
          vbArray[3] = value;
          break;
      }

      node.setAttribute("viewBox", vbArray.join(" "));
    } else if (typeof attribute === "object") {
      var _vbArray = vbString.split(" ");

      var x = attribute.x,
          y = attribute.y,
          w = attribute.w,
          h = attribute.h;

      if (x !== undefined) {
        _vbArray[0] = x;
      }

      if (y !== undefined) {
        _vbArray[1] = y;
      }

      if (w !== undefined) {
        _vbArray[2] = w;
      }

      if (h !== undefined) {
        _vbArray[3] = h;
      }

      node.setAttribute("viewBox", _vbArray.join(" "));
    } else {
      throw new Error("the attribute parameter must be string or object");
    }
  } else {
    throw new Error("the svg does not have the viewbox attribute set.");
  }
}