import stylesheet from "../../stylesheet";
[// structure
".wizard {\n        background: #fff;\n        position: relative;\n        padding: 1.5rem 0;\n        z-index: 0;\n    }", ".wizard .tab-pane {\n        position: relative;\n        width: 100%;\n        z-index: 1;\n    }", // disabled border-bottom line for .nav-tab (from core-ui/bootstrap)
".wizard .nav-tabs {\n        position: relative;\n        width: 100%;\n        z-index: 1;\n        display: flex;\n        justify-content: center;\n        margin-bottom: 1.5rem;\n        border: 0;\n        margin-bottom: 2rem;\n    }", // a wrapper places all items centered,
// and allows to add a connection line via after selection.
".wizard .nav-tabs .nav-item-wrapper {\n        display: flex;\n        position: relative;\n    }", // https://www.youtube.com/watch?v=htgr3pvBr-I
".wizard .nav-tabs .nav-item-wrapper:before {\n        content: \"\";\n        display: block;\n        position: absolute;\n        height: 0.5rem;\n        top: calc(50% - 0.25rem);\n        width: 80%;\n        left: 10%;\n        background: #e0e0e0;\n        z-index: -1;\n    }", // title
".wizard .title {\n        height: 3rem;\n        text-align: center;\n    }", ".wizard .title h3 {\n        font-size: 1.5rem;\n        padding-bottom: 1.5rem;\n    }", // cursors
".wizard .nav-item {\n        cursor: pointer;\n    }", ".wizard .nav-item.locked {\n        cursor: not-allowed;\n    }", // navigation items
".wizard .nav-item {\n        margin: 0rem 1.5rem;\n        background: #fff;\n    }", ".wizard .nav-item span {\n        display: inline-block;\n        line-height: 5rem;\n        width: 5rem;\n        height: 5rem;\n        font-size: 2rem;\n        border-radius: 50%;\n        padding: 0;\n        text-align: center;\n        border: 0.2rem solid #e0e0e0;\n    }", ".wizard .nav-item span i {\n        color:#555555;\n    }", ".wizard .nav-item span:hover {\n        color: #333;\n        border: 2px solid #333;\n    }", ".wizard .nav-item.active span {\n        border: 2px solid #5bc0de;\n    }", ".wizard .nav-item.active span i {\n        color: #5bc0de;\n    }", ".wizard .nav-item.active span,\n     .wizard .nav-item.active span:hover,\n     .wizard .nav-item.active span:focus {\n        cursor: pointer;\n        color: #555555;\n        border: 2px solid #5bc0de;\n    }"].forEach(function (rule) {
  return stylesheet.insertRule(rule);
});