var stylesheet = function () {
  var style = document.createElement("style");
  style.title = "l3p-frontend"; // Add the <style> element to the page

  document.head.appendChild(style);
  var stylesheet = Array.from(document.styleSheets).find(function (s) {
    return s.title === "l3p-frontend";
  });
  return stylesheet;
}();

export default stylesheet; // does not work for media querys!
// export function addRules(rules: String){

export function addRules(rules) {
  rules = rules.replace(/\s{2,}/g, '');
  rules = rules.match(/(.*?\{[^\}]*\})/g);
  rules.forEach(function (rule) {
    return stylesheet.insertRule(rule);
  });
}